import React from 'react'
import { css } from '@emotion/react'
import { Box, IconLink, Tooltip, theme } from 'fusion'

const container = css ({
    display: "flex",
    alignItems: "baseline",
    marginLeft: "-28px",
    marginBottom: "8px",
    "& .link": {
        height: "100%",
        marginRight: "8px",
        color: theme.colors.textSecondary,
        transition: "opacity .2s",
        opacity: 0,
        "&:hover": {
            color: theme.colors.text,
        },
    },
    "&:hover": {
        "& .link": {
            cursor: "pointer",
            opacity: 1,
        },
    },
    "& .h2": {
        marginBottom: "8px",
    },
})

// onLoad = {() => scrollToAnchor()}
// function scrollToAnchor() { window.location.hash=(location.pathname + "#" + props.anchor) }
// window.onload = () => { scrollToAnchor() }
// window.addEventListener('load', function () {   scrollToAnchor() }) 

export default function headerAnchor(props: any) {
    const [isCopied, setisCopied] = React.useState(false)
    function copyToClip() {
        navigator.clipboard.writeText(location.pathname + "#" + props.anchor)
        setisCopied(true)
    }
    return (
        <Box id={props.anchor} css={container}>
            {!isCopied &&
                <Tooltip
                    title="Copy link to heading" placement="left" style={{ fontSize: "12px" }} components={undefined} componentsProps={undefined} >
                    <a className="link" onClick={() => copyToClip()}  >
                        <IconLink size="medium" style={{ marginTop: "-8px" }} />
                    </a>
                </Tooltip>
            }
            {isCopied &&
                <Tooltip
                    title="Copied!" placement="left" style={{ fontSize: "12px" }} components={undefined} componentsProps={undefined} >
                    <a className="link" onMouseLeave={() => setisCopied(false)}>
                        <IconLink size="medium" style={{ marginTop: "-8px" }} />
                    </a>
                </Tooltip>
            }
            {props.children}
        </Box>
    )
}
